<template>
  <v-container>
    <v-row class="text-center justify-center">
      <v-col
        cols="12"
        v-for="(project, index) in paginatedList"
        :key="index"
        style="margin-bottom: 100px"
      >
        <v-card class="mx-auto pb-5 mb-5" dark>
          <v-toolbar
            :color="project.color"
            dark
            class="d-flex justify-center align-center"
            height="110"
          >
            <v-toolbar-title class="card-title">{{
              project.name
            }}</v-toolbar-title>

            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <a :href="project.url" target="_blank" v-if="project.url">
                  <v-icon v-bind="attrs" v-on="on"> mdi-link </v-icon>
                </a>
              </template>
              <span>Go to the site</span>
            </v-tooltip>
          </v-toolbar>

          <v-container fluid>
            <v-row justify="center">
              <v-col
                v-for="(image, index) in project.media"
                :key="index"
                cols="12"
                md="6"
              >
                <v-hover v-slot:default="{ hover }">
                  <v-card
                    :elevation="hover ? 24 : 2"
                    :class="{ 'on-hover': hover }"
                  >
                    <v-img
                      :src="image.thumb"
                      class="white--text align-end"
                      gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                      height="300px"
                      :class="hover ? 'zoom' : ''"
                      style="cursor: pointer"
                      @click="openGallery(index,project.offset)"
                    >
                      <v-card-title
                        class="justify-center"
                        v-text="image.caption"
                      ></v-card-title>
                    </v-img>
                  </v-card>
                </v-hover>

                <LightBox
                  ref="lightbox"
                  :media="project.media"
                  :show-caption="true"
                  :show-light-box="false"
                  previous
                  next
                />
              </v-col>
            </v-row>

            <v-row class="justify-center">
              <v-col cols="12">
                <v-dialog v-model="project.dialog" max-width="1000">
                  <template v-slot:activator="{ on, attrs }">
                    <v-card-actions class="justify-center">
                      <v-btn
                        class="button"
                        :color="project.color"
                        v-bind="attrs"
                        v-on="on"
                      >
                        <v-icon dark left> mdi-stack-overflow </v-icon>Tech
                        Stack
                      </v-btn>
                    </v-card-actions>
                  </template>

                  <v-card dark>
                    <v-card-title
                      :class="project.color"
                      class="card-title pa-6"
                    >
                      {{ project.name }} - Tech Stack
                    </v-card-title>

                    <v-card-text>
                      <ul>
                        <li
                          v-for="technology in project.stack"
                          :key="technology"
                          class="pa-5 mt-5 white--text link"
                        >
                          {{ technology }}
                        </li>
                      </ul>
                    </v-card-text>

                    <v-divider></v-divider>

                    <v-card-actions class="pa-6 justify-center">
                      <v-btn
                        :color="project.color"
                        width="300"
                        height="60"
                        class="pa-5"
                        @click="project.dialog = false"
                      >
                        <v-icon dark left> mdi-close </v-icon>Close
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-col>
            </v-row>
          </v-container>
        </v-card>
      </v-col>

      <v-pagination
        class="pagination mb-2"
        dark
        color="#848577"
        v-model="page"
        :length="pages"
        @input="updatePage"
      >
      </v-pagination>
    </v-row>
