  created() {
    this.initPage();
    this.updatePage(this.page);
  },
  methods: {
    initPage: function () {
      this.listCount = this.projects.length;
      if (this.listCount < this.pageSize) {
        this.paginatedList = this.projects;
      } else {
        this.paginatedList = this.projects.slice(0, this.pageSize);
      }
    },
    updatePage: function (pageIndex) {
      let start = (pageIndex - 1) * this.pageSize;
      let end = pageIndex * this.pageSize;
      this.paginatedList = this.projects.slice(start, end);
      this.page = pageIndex;
      window.scrollTo(0, 0);
    },
    openGallery: function (index,offset) {
      this.$refs.lightbox[offset].showImage(index);
    },
  },
  computed: {
    pages() {
      if (this.pageSize == null || this.listCount == null) return 0;
      return Math.ceil(this.listCount / this.pageSize);
    },
  },
